/**
 * todo reservoir sampling is better to support stream data
 * Algorithm R:
 * Vitter, Jeffrey S. (1 March 1985). "Random sampling with a reservoir" (PDF). ACM Transactions on Mathematical Software. 11 (1): 3757. CiteSeerX 10.1.1.138.784. doi:10.1145/3147.3165.
 */
export function reservoirSampling(dataSource) {
  var size = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : 500;
  if (dataSource.length <= size) return dataSource;
  var sampleSpace = dataSource.slice(0, size);
  var len = dataSource.length;

  for (var i = size + 1; i < len; i++) {
    var pos = Math.round(Math.random() * i);

    if (pos < size) {
      sampleSpace[pos] = dataSource[i];
    }
  }

  return sampleSpace;
}